import * as React from "react"
import { personalInfo } from "../data/resume"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faLinkedin, faSafari } from "@fortawesome/free-brands-svg-icons"

const IndexPage = () => {
  const links = [
    {show: personalInfo.linkedin, icon: faLinkedin, text: `/${personalInfo.linkedin}`, href: `https://www.linkedin.com/in/${personalInfo.linkedin}/`},
    {show: personalInfo.website, icon: faSafari, text: `${personalInfo.website}`, href: personalInfo.website},
  ]

  return (
    <>
      <main className="min-h-screen flex flex-col">
        <div className="m-auto flex flex-col pt-6 pb-24">
          <div className="flex flex-col">
            <span className="font-thin text-8xl">
              {personalInfo.firstName}
            </span>
            <span className="font-bold text-8xl -mt-4">{personalInfo.lastName}</span>
          </div>
          <ul className="flex flex-row space-x-6 mt-4">
            {links.map(({show, icon, text, href}) => {
              return show ? <li><a href={href}><FontAwesomeIcon icon={icon} /> <span className="text-gray-500">{text}</span></a></li> : ""
            })}
          </ul>
        </div>
      </main>
    </>
  )
}

export default IndexPage
